"use client";
import { useState } from "react";

export default function Register() {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [role, setRole] = useState("");
  const [apartmentInfo, setApartmentInfo] = useState(""); // for users
  const [servicesProvided, setServicesProvided] = useState(""); // for contractors
  const [experience, setExperience] = useState(""); // contractor
  const [contact, setContact] = useState(""); // contractor
  const [message, setMessage] = useState("");

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    try {
      const payload: any = { name, email, password, role };

      if (role === "user") payload.apartmentInfo = apartmentInfo;
      if (role === "contractor") {
        payload.servicesProvided = servicesProvided.split(","); // comma separated
        payload.experience = experience;
        payload.contact = contact;
      }

      const res = await fetch("http://localhost:5000/user/register", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      });

      const data = await res.json();
      if (data.success) {
        setMessage("Registered successfully! You can now login.");
        setName(""); setEmail(""); setPassword(""); setRole("");
        setApartmentInfo(""); setServicesProvided(""); setExperience(""); setContact("");
      } else {
        setMessage("Registration failed: " + data.error);
      }
    } catch (err) {
      console.error(err);
      setMessage("Something went wrong!");
    }
  };

  return (
    <div className="max-w-md mx-auto p-8 border rounded shadow mt-10">
      <h1 className="text-2xl font-bold mb-4 text-center">Register</h1>
      <form onSubmit={handleSubmit} className="flex flex-col gap-4">
        <input type="text" placeholder="Name" value={name} onChange={e => setName(e.target.value)} required className="border p-2 rounded"/>
        <input type="email" placeholder="Email" value={email} onChange={e => setEmail(e.target.value)} required className="border p-2 rounded"/>
        <input type="password" placeholder="Password" value={password} onChange={e => setPassword(e.target.value)} required className="border p-2 rounded"/>
        <select value={role} onChange={e => setRole(e.target.value)} required className="border p-2 rounded">
          <option value="">Select Role</option>
          <option value="user">Apartment Member</option>
          <option value="contractor">Contractor</option>
        </select>

        {role === "user" && (
          <input type="text" placeholder="Apartment Info (Block, Flat No.)" value={apartmentInfo} onChange={e => setApartmentInfo(e.target.value)} required className="border p-2 rounded"/>
        )}

        {role === "contractor" && (
          <>
            <input type="text" placeholder="Services Provided (comma separated)" value={servicesProvided} onChange={e => setServicesProvided(e.target.value)} required className="border p-2 rounded"/>
            <input type="text" placeholder="Experience (in years)" value={experience} onChange={e => setExperience(e.target.value)} required className="border p-2 rounded"/>
            <input type="text" placeholder="Contact Number" value={contact} onChange={e => setContact(e.target.value)} required className="border p-2 rounded"/>
          </>
        )}

        <button type="submit" className="bg-blue-500 text-white p-2 rounded hover:bg-blue-600">Register</button>
      </form>
      {message && <p className="mt-4 text-center text-green-600">{message}</p>}
    </div>
  );
}
